:root{
    --mainColor: #282c34;
    --mainColorLight: #5767aa;
    --secondaryColor: #DB2B39;
    --textColor: #eee;
}


header{
    font-size: calc(5px + 1.4vmin);
    background-color: var(--mainColor);
    display: flex;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    color: var(--textColor);
    position: relative;
}
header div h3{
    font-size: 1.5rem;
}
nav a {
    margin: 0 2rem;
    color: var(--textColor);
    text-decoration: none;
}

nav a:hover{
    color: var(--secondaryColor);
}

header .nav-btn{
    padding: 15px;
    font-size: 1.5rem;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
}


@media only screen and (max-width: 1024px){
    header .nav-btn{
        visibility: visible;
        opacity: 1;
    }
    
    .navbar {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%; 
        height: 100vh;
        z-index: 1000; /* Asegura que est√© por encima de otros elementos */
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
      }
      
     
    header nav{
        position: fixed;
        top: 0;
        left: 0; 
        gap: 1.5rem;
        background-color: var(--mainColor);
        transition: 1s;
        transform: translateY(-100vh);
    }
    header .responsive_nav{
        transform: none;
        display: flex !important;
    }

}